import React, { Component } from 'react'
import './style.css'
export default class PageRegistration extends Component {
  render() {
    const name = localStorage.getItem('name')
    
    return (
        <div>
            <h1>Welcome {name}</h1>
            <div className = 'basket'>
              <h4>Корзина</h4>
            </div>

        </div>
    );
  }
}

